.socialIcons-container {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
}
.socialIcons-container .icon-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin: 2px 0px;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  background-color: #8CB9B4;
  cursor: pointer;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
  text-decoration: none;
  color: black;
}
.socialIcons-container .icon-container .iconName {
  display: none;
  margin-right: 2px;
}
.socialIcons-container .icon-container .icon {
  border: 1px black solid;
  padding: 4px;
  border-radius: 100%;
}
.socialIcons-container .icon-container:hover .iconName {
  display: block;
}
.socialIcons-container .icon-container:hover .icon {
  transition: -webkit-transform 1.5s ease-in-out;
  transition: transform 1.5s ease-in-out;
  transition: transform 1.5s ease-in-out, -webkit-transform 1.5s ease-in-out;
  transform: rotate(360deg);
  -ms-transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
}
.socialIcons-container .icon-container:hover.gitHub {
  background-color: #8a8a8a;
}
.socialIcons-container .icon-container:hover.linkedin {
  background-color: #23648c;
}
.socialIcons-container .icon-container:hover.insta {
  background-image: linear-gradient(100deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}/*# sourceMappingURL=socialIcons.css.map */
